import { FieldType } from '../models';

export const /** @type {?} */ testFilterCondition = (operator: string, value1: any, value2: any): boolean => {
  switch (operator) {
    case '<': return (value1 < value2);
    case '<=': return (value1 <= value2);
    case '>': return (value1 > value2);
    case '>=': return (value1 >= value2);
    case '!=':
    case '<>': return (value1 !== value2);
    case '=':
    case '==': return (value1 === value2);
  }
  return true;
};
